@use "global" as *;

.p-cuisine {
  margin-top: 30px;
  text-align: center;

  @include mq("md") {
    margin-top: 173px;
  }
}
.p-cuisine__content-l {
  margin-top: 15px;
  position: relative;

  & .p-cuisine__button {

    @include mq("md") {
      margin-left: 742px;
      margin-top: -60px;
    }
  }
}
.p-cuisine__content-l::after {
  content: "10th Floor";
  color: $blue;
  font-family: Imperial Script;
  font-size: 36px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  transform: rotate(-90deg);
  position: absolute;
  bottom: 30px;
  right: 0;

  @include mq("md") {
    font-size: 96px;
    transform: none;
    top: 0;
    bottom: auto;
    right: 154px;
  }
}
.p-cuisine__card {
  grid-column: 2 / 4;

  @include mq("md") {
    grid-column: 2 / 3;

  }
  &--mirror {
    grid-column: 3 / 5;

    @include mq("md") {
      grid-column: 4 / 5;

    }
  }
}
.p-cuisine__gallery {
  background-image: linear-gradient(transparent 0% 15%, $gold 15% 82%, transparent 82% 100%);
}
.p-cuisine__button {
  margin-inline: auto;


}
.p-cuisine__content-r {
  margin-top: 50px;
  position: relative;

  @include mq("md") {
    margin-top: 313px;
  }
  & .p-cuisine__button {

    @include mq("md") {
      margin-right: 742px;
      margin-top: -60px;
    }
  }
}
.p-cuisine__content-r::before {
  content: "12th Floor";
  color: $blue;
  font-family: Imperial Script;
  font-size: 36px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  writing-mode: vertical-lr;
  position: absolute;
  bottom: 60px;
  left: 0;

  @include mq("md") {
    font-size: 96px;
    writing-mode: initial;
    top: 0;
    bottom: auto;
    left: 154px;
  }
}

.p-cuisine__card--alone {

  @include mq("md") {
    grid-row: 1 / 3;
    margin: auto;
  }
}